document.querySelector('#btn-play').addEventListener('click', () => {
  if (input_name.value.trim().length > 0) {
      alert(`'${input_name.value} you can start the game'` )
  } else {
    console.log('error')
    input_name.classList.add('input-err');
    setTimeout(() => {
      input_name.classList.remove('input-err');
      input_name.focus();
    }, 500);
  }
})


const getGameInfo = () => JSON.parse(localStorage.getItem('game'));

const init = () => {
  const darkmode = localStorage.getItem('darkmode');
  document.body.classList.add(darkmode ? '' : 'dark');

  const game = getGameInfo();

  document.querySelector('#btn-continue').style.display ? 'grid' : 'none';
}

init();
